
@import url("//unpkg.com/element-ui/lib/theme-default/index.css");
.avatar-uploader {
    text-align: left;
}
.avatar-uploader .el-upload__inner {
    display: inline-block;
    position: relative;
    background-color: #f9fafc;
    border: 1px solid #c0ccda;
    box-sizing: border-box;
    width: 180px;
    height: 180px;
    border-radius: 4px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    text-align: center;
}
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}
.avatar-uploader .el-upload:hover {
    border-color: #20a0ff;
}
.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
}
.avatar {
    width: 178px;
    height: 178px;
    display: block;
}
input.el-upload__input {
    opacity: 0;
}
#filePicker_icon .webuploader-pick {
    width: 220px;
    height: 220px;
    background: url('../../assets/upImg.png') no-repeat center;
}



